	module.exports = {"data":{"postsJson":{"title":"소개사도행전","questionsTitle":null,"display":"none","bookName":"사도행전","ref":null,"html":"<h6>모든 사람들에게 복음을</h6><p>아직도 세상을 흔들고 있는 폭발</p><h6>사도행전을 어떻게 읽을 것인가</h6><p>예수님이 돌아가신 후에 그 분의 사역은 초대교회 교인들의 삶 속에 계속되었고 오늘날에도 그 분의 백성들의 삶을 통해 지속되고 있다. 사도행전은 성령의 능력으로 예수님의 선교적 가르침을 그들의 시대 속에서 살아내는 것이 무엇을 의미하는지를 실천하고자 노력했던 초기 신자들의 이야기이다. 그들의 이야기는 우리에게 격려가 된다: 어려움에도 불구하고 교회는 생존할 수 있으며 심지어 번성할 수 있다. 변혁, 부흥, 교회 성장은 인간의 노력만이 아닌 성령님과의 협력으로 이루게 된다.<br/> 30년의 역사를 다루고 있지만 각 페이지마다 정밀하고 세세한 내용들과 극적인 묘사들로 가득하다. 첫 장들은 어떻게 교회가 성령님의 임재와 능력으로 배가되기 시작했는가를 말해준다. 복음이 유대인과 이방인 사이의 오래된 문화적 벽을 허물게 되자 강력한 성장이 일어났다. 그러자 교회는 지리적으로 예루살렘을 훨씬 넘어서까지 그 증언을 확장시켰다. 베드로와 바울이 이 이야기들의 핵심 인물들이다: 베드로는 예루살렘에 있는 유대인 신자들의 정신적 지주였고 바울은 이방을 향한 교회의 첫 번째 위대한 선교사로서 새로운 땅들로 나아갔다.<br/> 우리는 예수님께서 이 신자들에게 처음 위탁하셨던 그 약속과 사명을 물려받았다. 사도행전에서 매우 활동적이셨던 바로 그 성령님이 오늘날 우리의 이야기 안에도 여전히 역사하고 계신다. 그러니 우리도 초대교회의 형제자매들이 가졌던 열심과 믿음에 가까이 접근하자. 이제는 우리가 “땅 끝까지”(행1:8) 그분의 증인이 될 차례이다.</p><h6>이 책은 누가 언제 썼는가?</h6><p>누가복음의 저자인 누가가 주후 63-70년 경에 사도행전을 저술했다. 누가복음과 사도행전은 신약에서 거의 4분의 1가까이 되는 분량을 차지한다.</p><h6>누구를 대상으로 어떤 목적으로 썼는가?</h6><p>누가는 역사학자로서 데오빌로(“하나님을 사랑하는 자”라는 뜻을 가진 이름)에게 예수님의 부활 이후에 일어난 일들을 전하기 위해 이 책을 썼다. 사도행전은 복음서의 2부, 즉 누가복음의 후속편이다. 사도행전에서 누가는 아마도 행정당국에 교회를 정당화하려고 하거나 또는 신자들의 믿음을 확증하기 위해서 기독교의 놀라운 성장을 설명한다. 또한 누가는 유대인들과 이방인들 간의 오래 된 갈등을 없애기 위해 성령께서 어떻게 일하셨는지를 신자들에게 이해시키고자 했을 수도 있다.</p><h6>SourceView 통찰</h6><p>사도행전에는 베드로와 바울 두 명의 주연배우들이 있다. 베드로는 총 36회 대사가 있는데(2,376단어) 그 중31번은 혼자, 5번은 다른 이들과 함께 말한다. 바울은 총 56번 말하는데(3,708단어) 그 중에서 53번은 혼자, 3번은 다른 사람들과 함께 말한다. 이 두 사람의 말이 사도행전 본문의 4분의 1 이상을 차지한다. 아래 평행 부분들을 생각해 보자:</p><br/><table style='max-width:800px; margin: 0 auto; font-size: 18px'> <tr> <th colspan='3' style='text-align: center'>사도 행전에서 베드로와 바울 사이의 평행</th> </tr><tr> <th style='width: 50%; font-weight: bold'>둘다…</th> <th style='width: 25%; font-weight: bold'>베드로</th> <th style='width: 25%; font-weight: bold'>바울</th> </tr><tr> <th>태어나면서 불구를 고치다</th> <th>3:6</th> <th>14:10</th> </tr><tr> <th>음모를 당하다</th> <th>4:1-3; 5:17-18; 12:2-5</th> <th>9:23-25,29; 13:45-50; 14:19; 16:19-39; 17:5-9,13; 19:23-41; 20:3; 21:27-35; 23:12-15; 25:2-3</th> </tr><tr> <th>유대 공의회에서 간증하다</th> <th>4:5-22; 5:17-41</th> <th>22:30-23:10</th> </tr><tr> <th>특출한 기적을 행하다</th> <th>5:15-16</th> <th>19:11-12</th> </tr><tr> <th>감옥에서 초자연적으로 풀려나다</th> <th>5:18-25; 12:6-16</th> <th>16:23-40</th> </tr><tr> <th>핍박가운데에서도 담대히 복음을 </th> <th>5:21</th> <th>14:20</th> </tr><tr> <th>성령 세례를 위한 기도를 하다</th> <th>8:17</th> <th>19:6</th> </tr><tr> <th>마술사를 만나다</th> <th>8:18-24</th> <th>13:8-11</th> </tr><tr> <th>죽은 자를 살리다</th> <th>9:36-42</th> <th>20:7-12</th> </tr><tr> <th>“이방인”에 대한 환상을 보다</th> <th>10:10-16</th> <th>16:9-10</th> </tr><tr> <th>사람에 의해 경배를 받다</th> <th>10:25</th> <th>14:11-18</th> </tr><tr> <th>로마 군인의 가족에서 복음을 전하다</th> <th>10:24</th> <th>16:31</th> </tr><tr> <th>'전환'이 처음으로 들려 왔습니다</th> <th>10:1-47</th> <th>9:1-22</th> </tr><tr> <th>'전환'이 2 번 말함</th> <th>11:4-18</th> <th>22:3-21</th> </tr><tr> <th>'전환'은 세 번째로 말했다.</th> <th>15:7-11</th> <th>26:2-23</th> </tr></table><br /><p>이 두 명의 주인공 각 각이 하는 가장 긴 설교를 읽고 비슷한 점과 다른 점에 대해 묵상해 보라. 베드로의 주요 설교들은 다음과 같다.</p><ul><br /><li>오순절 날 예루살렘 성전에서의 설교 (행1:20-2:39)</li><br /><li>태어나면서 불구되었던 자를 치유한 후 예루살렘 성전에서의 설교(행3:6-12)</li><br /><li>고넬료를 만나고 난 후 예루살렘에서 믿는 자들에게 한 그의 간증(행10:47-11:5)</li></ul><br/><p>바울의 주요한 설교들은 다음과 같다.</p><ul><br /><li>첫번째 선교여행중 비시디아 안디옥의 회당에서 한 그의 설교 (행12:16-41)</li><br /><li>그가 체포되던 때에 예루살렘 성전에서 한 그의 간증(행22:1-25)</li><br /><li>가이사랴에서 아그립바왕과 베스도 총독앞에서 한 그의 간증(행26:2-23)</li></ul><br/><p>베드로와 바울외에, 사도행전에서는 개인, 그룹, 인간, 마귀와 천사들을 포함하여 61명이 말한다. 사도행전의 이 파란색 부분들은 책의 30%를 차지한다. 세 개의 가장 긴 파란색 부분은 다음과 같다.</p><br/><ul><li>유대 공회앞에서 송소를 당할 때 예루살렘에서 한 스데반의 설교(행7:2-53)</li><br /><li>바울의 사건에 대해 헤롯 아그립바에게 하는 베스도의 설명(행25:14-21)</li><br /><li>예루살렘 공회의 결론에 대한 야고보의 결정(행15:13-21)</li></ul>","path":"/351","Question1":null,"Question2":null,"Question3":null,"Question4":null,"prevPath":"/338","nextPath":"/352"}},"pathContext":{}}

